* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.ca197847.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Bold.fdb9b54a.ttf");
  font-weight: 700;
  font-style: normal;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  margin-right: 20px;
  display: flex;
  position: relative;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  text-align: center;
  color: #060b35;
  align-items: center;
  height: 100%;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: #00acdc;
}

.button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  transition: all .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__reviews {
  justify-content: center;
  align-items: center;
  margin-top: auto;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price_bold {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.container {
  grid-template: auto / 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template: auto / repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template: auto / repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template: auto / repeat(4, 200px);
  }
}
